import React from "react"
import { MDXRenderer } from "gatsby-plugin-mdx"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { graphql } from "gatsby"
// Libs
import getPageData from "globalLibs/helpers/get-page-data"
// Atoms
import Seo from "globalAtoms/Seo"
// Organisms
import Layout from "globalOrganisms/Layout"
import HomePageCover from "projectOrganisms/HomePageCover"
import Sidebar from "projectOrganisms/Sidebar"
import MainNav from "projectOrganisms/MainNav"

export default function PortfolioSingle(props) {
  const pageData = getPageData(props)

  return (
    <Layout className="flex-col">
      <Sidebar />
      <MainNav />
      <Seo pageData={pageData} ogType="article" />
      <HomePageCover style={{ height: "400px" }}>
        <h1
          className="flex flex-col text-center absolute z-10 inset-0 text-hight-contrast mx-4"
          style={{ top: "24%" }}
        >
          <span className="xxxLarge-high-title font-bold mb-4">
            {pageData.frontmatter.title}
          </span>
          <span className="xxxLarge-h1">{pageData.frontmatter.caption}</span>
        </h1>
      </HomePageCover>
      <article className="container mx-auto max-w-screen-lg -mt-12">
        <GatsbyImage
          width="992"
          aspectRatio={4 / 3}
          alt={`${pageData.frontmatter.title}`}
          image={getImage(pageData.frontmatter.cover)}
          className="rounded"
          style={{
            boxShadow: `1px 0px 0px var(--separator-non-opaque),
          -1px 0px 0px var(--separator-non-opaque),
            0px 1px 0px var(--separator-non-opaque),
            0px -1px 0px var(--separator-non-opaque)`,
          }}
        />
        <div className="flex justify-between items-end mt-12">
          <h2 className="large-h1 m-0">Detailed information</h2>
          {pageData.frontmatter.extra.workExternalLink && (
            <span>
              Watch in:{" "}
              <a
                href={pageData.frontmatter.extra.workExternalLink.href}
                target="blank"
              >
                {pageData.frontmatter.extra.workExternalLink.name}
              </a>
            </span>
          )}
        </div>
        <hr className="mt-4 mb-6" />
        <MDXRenderer>{pageData.body}</MDXRenderer>
      </article>
    </Layout>
  )
}

export const query = graphql`
  query ($slug: String!) {
    mdx(slug: { eq: $slug }) {
      ...PortfolioSinglePage
    }
  }
`
